<template>
    <div class="Recharge">
        <div style="margin: 20px">
            <h2>充值</h2>
        </div>
        <Form ref="formInline" :model="formInline" :rules="ruleInline">
            <FormItem prop="username">
                <Input type="text" v-model="formInline.username" autofocus placeholder="工号">
                <Icon type="ios-contact-outline" slot="prepend"></Icon>
                </Input>
            </FormItem>
            <FormItem prop="card_number">
                <Input type="text" v-model="formInline.card_number" placeholder="充值卡号码">
                <Icon type="ios-barcode-outline" slot="prepend"></Icon>
                </Input>
            </FormItem>
            <FormItem>
                <Button type="primary" @click="handleSubmit('formInline')">充值</Button>
                <Poptip trigger="hover" title="点击购卡">
                    <a style="margin-left: 20px;">点击购卡</a>
                    <!-- <img slot="content" width="128px" src = 'https://i.bmp.ovh/imgs/2022/06/18/0272e507ad97bb02.jpg' /> -->
                    <div slot="content" style="line-height: 48px;">
                        <div>
                            <Button to="http://mtw.so/5yyjVq" type="info" long>单次充值(1次)</Button>
                        </div>
                        <div>
                            <Button to="http://mtw.so/63nK6L" type="info" long>季度充值(3次)</Button>
                        </div>

                    </div>
                </Poptip>
            </FormItem>
        </Form>
    </div>
</template>
<script>
export default {
    data() {
        return {
            formInline: {
                username: "",
                card_number: "",
            },
            isVip: false,
            ruleInline: {
                username: [
                    {
                        required: true,
                        message: "请输入工号",
                        trigger: "blur",
                    },
                ],
                card_number: [
                    {
                        required: true,
                        message: "请输入充值卡号码",
                        trigger: "blur",
                    },
                    {
                        type: "string",
                        pattern: /^[a-zA-Z0-9]*$/,
                        message: "只能输入数字和字母",
                        trigger: "blur",
                    },
                ],
            },
        };
    },
    methods: {
        handleSubmit(name) {
            this.$refs[name].validate((valid) => {
                if (valid) {
                    let app_id = 2;
                    let secret_key = "DIy5BmbglwD4uQxwjYaplaBJLO5tQmYL";
                    this.$axios.get("http://verify.smallest.com.cn/app_user_api/userrecharge", {
                        params: {
                            app_id: app_id,
                            secret_key: secret_key,
                            username: 'kaoheyun' + this.formInline.username,
                            card_number: this.formInline.card_number,
                        }
                    }).then(res => {
                        if (res.data.code) {
                            this.$Modal.success({
                                title: "充值成功",
                                content: "充值成功，您当前可用次数为：\n" + res.data.data.surplus_value,
                                onOk: () => {
                                    this.$router.push("/");
                                }
                            });
                        } else {
                            this.$Modal.error({
                                title: "充值失败",
                                content: res.data.content,
                            });
                        }
                    }).catch(err => {
                        console.log(err);
                    });
                }
            });
        },
    },
};
</script>