<template>
  <div class="home">
    <img alt="logo" src="../assets/logo.png" />
    <p class="left" :style="{ 'color': isVip ? 'darkorange' : '' }">工号：{{ username }}</p>
    <p class="left" v-if="isVip">剩余使用次数：{{points}}</p>
    <p class="left">请选择考场：</p>
    <Cascader :data="basicList" @on-change="handleChange" :load-data="loadData"></Cascader>

    <div v-if="selectType === '0'">
      <Input search v-model="keyword" placeholder="请输入要搜索的问题" clearable style="margin: 26px auto"
        @on-change="handleSearch"></Input>
      <div v-show="selectType === '0' && listall">
        <div class="questionsList" style="padding: 10px; text-align: left" v-for="(item, index) in questionList"
          :key="index">
          <Card shadow>
            <div class="questions" v-html="index + 1 + '、' + item.qon"></div>
            <div class="choices" v-html="item.qos"></div>
            <div class="tips">
              答案：<span class="answer">{{ item.qanswer }}</span>
            </div>
          </Card>
        </div>
      </div>
      <div v-show="selectType === '0' && !listall">
        <Timeline>
          <TimelineItem v-for="(item, index) in resultList" :key="index" style="text-align: left">
            <Card>
              <div class="questions" v-html="index + 1 + '、' + item.qon"></div>
              <div class="choices" v-html="item.qos"></div>
              <div class="tips">
                答案：<span class="answer">{{ item.qanswer }}</span>
              </div>
            </Card>
          </TimelineItem>
        </Timeline>
        <Alert type="warning" show-icon v-if="resultList.length === 0" class="left">
          未找到相应题目
          <template slot="desc">
            没有找到相关题目，请更改关键字重试。
          </template>
        </Alert>
      </div>
    </div>
    <div class="left" style="margin: 26px auto" v-if="selectType > 0">
      <div v-for="(items, key) in answersList" style="padding: 10px" :key="key">
        <div v-if="items.items.length > 0">
          <div class="title">
            <Divider orientation="left">{{ items.title }}</Divider>
          </div>
          <div>
            <span v-for="(item, idx) in items.items" :key="idx">
              <template v-if="key == 'list1' || key == 'list3'">
                <div v-if="idx % 5 == 0">
                  {{ idx + 1 }}-{{
                      items.items.length > idx + 5 ? idx + 5 : items.items.length
                  }}
                </div>
                {{ item.qasw }}
              </template>
              <div v-else>{{ item.tid }}、{{ item.qasw }}</div>
            </span>
          </div>
        </div>
      </div>
    </div>
    <BackTop></BackTop>
  </div>
</template>

<style>
.left {
  text-align: left;
}

.answer {
  color: red;
  font-size: 20px;
  vertical-align: middle;
}

.light {
  color: red;
  background-color: yellow;
  font-weight: bold;
}
</style>

<script>
export default {
  name: "HomeView",
  components: {},
  mounted() {
    if (typeof this.$route.params.users === "undefined") {
      this.$router.push("/login");
    } else {
      this.userid = this.$route.params.users.userid;
      this.username = this.$route.params.users.username;
      this.isVip = this.$route.params.isVip || false;
      this.points = this.$route.params.points || 0;
      let Url =
        "http://yun.hbgs.online:8081/kaoshi/index.php?content-phone-muinews-findbasic";
      let data = this.$qs.stringify({ userid: this.userid });
      this.$axios.post(Url, data).then((rsp) => {
        if (rsp.data.statusCode === 200) {
          //成功读取
          let basics = rsp.data.basics;
          for (let p in basics) {
            let basicknows = rsp.data.basics[p].basicknows;
            let basicknowids = [];
            for (let q in basicknows) {
              for (let id in basicknows[q]) {
                let knowid = basicknows[q][id];
                basicknowids.push(knowid);
              }
            }
            let basic = {
              value: basics[p].basicid,
              label: basics[p].basic,
              children: [],
              loading: false,
              basicknows: basicknowids,
            };
            this.basicList.push(basic);
          }
        } else {
          this.$Message.error("获取考场信息失败，请重新登录尝试");
          this.$router.push("/login");
        }
      });
    }
  },
  data() {
    return {
      userid: null,
      points: 0,
      username: "",
      isVip: false,
      basicList: [],
      selectType: -1,
      listall: true,
      keyword: "",
      basicknowids: [],
      questionList: [],
      resultList: [],
      answersList: [],
      timer: "",
    };
  },
  methods: {
    checkVip(users) {
      // console.log(users);
      let app_id = 2;
      let secret_key = "DIy5BmbglwD4uQxwjYaplaBJLO5tQmYL";
      let username = 'kaoheyun' + users.username;
      let password = 'kaoheyun' + users.userid;
      this.$axios.get("http://111.229.103.199/app_user_api/userregister", {
        params: {
          app_id: app_id,
          secret_key: secret_key,
          username: username,
          password: password,
          enpassword: password,
        }
      }).then(() => {
        this.$axios.get("http://111.229.103.199/app_user_api/getusertoken", {
          params: {
            app_id: app_id,
            secret_key: secret_key,
            username: username,
            password: password,
          }
        }).then((res) => {
          if (res.data.code) {
            let token = res.data.data.token;
            this.$axios.get("http://111.229.103.199/app_user_api/setuserdec", {
              params: {
                app_id: app_id,
                token: token,
                points: 0,
              }
            }).then((res) => {
              if (res.data.code) {
                this.$axios.get("http://111.229.103.199/app_user_api/getuserapp", {
              params: {
                app_id: app_id,
                token: token,
                device_code: username,
              }
            }).then((res) => {
              if(!res.data.code){
                this.isVip = false;
              }
              this.points = res.data.data.points;
            });
              } else {
                this.isVip = false;
              }
            });
          } else {
            this.$message.error(res.data.content);
          }
        });
      });
    },
    handleChange(value, selectData) {
      // value[0] 考场Id
      // value[1] 功能类型
      if (value.length === 0) return;
      let basicid = value[0];
      let typeid = value[1];
      this.selectType = typeid;
      switch (typeid) {
        case "0": {
          let basicknows = selectData[0].basicknows;
          this.getLessons(basicid, basicknows);
          this.searchShow = true;
          break;
        }
        case "1": {
          this.checkVip(this.$route.params.users);
          if (this.isVip === false) {
            this.answersList = [];
            this.$Modal.info({
              title: "提示",
              content: "您还不是VIP会员，无法使用此功能，请联系管理员开通VIP会员。",
              onOk: () => {
              },
            });
          } else {
            this.$Modal.info({
              title: "模拟考试",
              content: "答案仅供参考，不保证其一定准确。请酌情使用。",
              onOk: () => {
                this.getExamPaper(this.userid, basicid, typeid);
              },
            });
          }
          break;
        }
        case "2": {
          this.checkVip(this.$route.params.users);
          if (this.isVip === false) {
            this.answersList = [];
            this.$Modal.info({
              title: "提示",
              content: "您还不是VIP会员，无法使用此功能，请联系管理员开通VIP会员。",
              onOk: () => {
              },
            });
          } else {
            this.$Modal.info({
              title: "正式考试",
              content:
                "正式考试答题仅一次机会，请尽快进行答题。答案仅供参考，不保证其一定准确。请酌情使用。",
              onOk: () => {
                this.getExamPaper(this.userid, basicid, typeid);
              },
            });
          }
          break;
        }
        default: {
          return;
        }
      }
    },
    handleSearch() {
      clearTimeout(this.timer);
      this.timer = setTimeout(() => {
        this.listall = false;
        if (this.keyword == "") {
          this.resultList = [];
          this.listall = true;
          return;
        }
        let lists = JSON.parse(JSON.stringify(this.questionList));
        this.resultList = [];
        lists.forEach((item) => {
          if (
            item.qon.indexOf(this.keyword) > -1 ||
            item.qos.indexOf(this.keyword) > -1
          ) {
            this.resultList.push(item);
          }
        });
        this.resultList.map((item) => {
          item.qon = this.brightKeyword(item.qon);
          item.qos = this.brightKeyword(item.qos);
        });
      }, 1000);
    },
    loadData(item, callback) {
      //动态加载内容
      item.loading = true;
      let Url =
        "http://yun.hbgs.online:8081/kaoshi/index.php?content-phone-muinews-getexaminfonews";
      let data = this.$qs.stringify({
        userid: this.userid,
        basicid: item.value,
        ehtype: 2,
      });
      this.$axios.post(Url, data).then((rsp) => {
        let children = [
          {
            value: "0",
            label: "全部题库",
          },
          {
            value: "1",
            label: "模拟考试",
          },
        ];
        if (rsp.data.statusCode === 200) {
          children.push({
            value: "2",
            label: "正式考试",
          });
        }
        setTimeout(() => {
          item.children = children;
          item.loading = false;
          callback();
        }, 500);
      });
    },
    getLessons(basicid, basicknows) {
      this.questionList = [];
      let Url =
        "http://yun.hbgs.online:8081/kaoshi/index.php?content-phone-muiphone-getlessonquestion";
      basicknows.forEach((knowsid) => {
        let qutypes = [1, 2, 3, 6, 7]; //开放1,2,3,6,7五种题型
        qutypes.forEach((qutype) => {
          let data = this.$qs.stringify({
            basicid: basicid,
            knowsid: knowsid,
            qutype: qutype,
          });
          this.$axios.post(Url, data).then((rsp) => {
            //解析数据
            this.barsconvert(rsp.data.args);
          });
        });
      });
    },
    getExamPaper(userid, basicid, ehtype) {
      let Url =
        "http://yun.hbgs.online:8081/kaoshi/index.php?content-phone-muinews-findexampaperByRedis";
      let data = this.$qs.stringify({
        userid: userid,
        basicid: basicid,
        ehtype: ehtype,
      });
      this.$axios.post(Url, data).then((rsp) => {
        if (rsp.data.statusCode === 200) {
          this.answersList = this.convertBars(
            rsp.data.sessionvars,
            rsp.data.questype
          );
        } else {
          this.$Notice.error({
            title: "获取试卷失败",
            desc: rsp.data.message,
          });
        }
      });
    },
    convertBars(item, questype) {
      let newNews = {
        list1: [],
        list2: [],
        list3: [],
        list6: [],
        list7: [], //目前只开放一二三六七 五种题型。
      };
      let questypelite = item.examsessionsetting.examsetting.questypelite;
      let questions = item.examsessionquestion.questions;
      for (let p in questypelite) {
        if (questypelite[p] == 1) {
          let items = [];
          let inti = 0;
          for (let q in questions[p]) {
            inti++;
            items.push({
              id: questions[p][q]["questionid"],
              tid: inti,
              qon: this.strReplace(
                this.entityToString(questions[p][q]["question"])
              ),
              qasw: questions[p][q]["questionanswer"],
            });
          }
          switch (p) {
            case "1": {
              newNews.list1 = {
                title: questype[p].questype,
                items: items,
              };
              break;
            }
            case "2": {
              newNews.list2 = {
                title: questype[p].questype,
                items: items,
              };
              break;
            }
            case "3": {
              newNews.list3 = {
                title: questype[p].questype,
                items: items,
              };
              break;
            }
            case "6": {
              newNews.list6 = {
                title: questype[p].questype,
                items: items,
              };
              break;
            }
            case "7": {
              newNews.list7 = {
                title: questype[p].questype,
                items: items,
              };
              break;
            }
            default: {
              continue;
            }
          }
        }
      }
      return newNews;
    },
    barsconvert(items) {
      let questions = items.questions;
      let barids = [];
      for (let p in questions) {
        barids = {
          id: questions[p]["questionid"],
          qon: this.strReplace(this.entityToString(questions[p]["question"])),
          qos: questions[p]["questionselect"].replace(/&lt;[^&gt;]+&gt;/g, ""),
          qanswer: this.strReplace(
            this.entityToString(questions[p]["questionanswer"])
          ),
        };
        this.questionList.push(barids);
      }
    },
    entityToString(entity) {
      var div = document.createElement("div");
      div.innerHTML = entity;
      var res = div.innerText || div.textContent;
      return res;
    },
    strReplace: function (entity) {
      var result = entity;
      var key = "</p>[^<]*?<p>";
      result = result.replace(new RegExp(key, "g"), "<br>");
      result = result.replace("<p>", "");
      result = result.replace("</p>", "");
      return result;
    },
    brightKeyword(val) {
      let keyword = this.keyword;
      if (val.indexOf(keyword) !== -1) {
        return val.replace(
          new RegExp(keyword, "g"),
          `<span class='light'>${keyword}</span>`
        );
      } else {
        return val;
      }
    },
  },
};
</script>
